.topmain {
    position: relative;
    height: 100vh;
    width: 100vw;
    border-radius: 1rem;
    background: hsl(0, 0%, 91%);
    overflow: hidden;
    display: flex;
    box-shadow: 2px 2px 8px var(--grayText);
    display: flex;
    margin: auto;
    padding: 1rem;
    max-width: 100vw;

    .cardcontainer {
        border: none;
        padding: 3rem;
    }
}

